
body {
    margin: 10;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* background-color: #76FF7A; */
    background-image: url(https://images.pexels.com/photos/1420701/pexels-photo-1420701.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
      background-size: 106%;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }

.App{
    width: 90vw;
    height: 90vh;
  }
  
  .btn-position{
    margin-left: 10%;
    width: 80%;
    margin-left: none;
    height: 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .grp-name{
    margin-top: 30%;
    font-style: Cursive;
    font-weight: 500;
    font-size: 20px;
  }
  
  .btn{
    height: 80%;
    margin-top: 30%;
    width: 8%;
    border: 3px solid grey;
    border-radius: 10px;
    background: rgba(255, 255, 255,0.2);
  }
  
  #button-container{
    width: 23%;
    height: 15%;
    margin-left: 50%;
    margin-top: 23%;
  }
   
  .hover-btn{
    height: 100%;
    width: 100%;
    font-size: 2.5rem;
  
  }
  
  .copyright{
    font-size: 1rem;
    font-weight: 350;
    margin-left: 85%;
    margin-top: 16%;
    width: 24%;
    background-color:  rgba(105, 23, 227, 0.474);
    padding: 3px 3px 3px 7px;
    border-radius: 7px;
  }
  
  .top{
    height: 30%;
    width: 50%;
    margin-top: -50%;
    margin-left: 20%;
    padding: 6px 10px 15px 10px;
    border: 1px solid black;
    border-radius: 10px;
    font-size: 1rem;
    background-color: rgba(104, 23, 227, 0.1);
    
  }
  
  .right{
      height: 30%;
      width: 50%;
      margin-top:-22%;
      margin-left: 105%;
      padding: 6px 10px 15px 10px;
      border: 1px solid black;
      border-radius: 10px;
      font-size: 1rem;
      background-color: rgba(104, 23, 227, 0.1);
  }
  
  .down{
    height: 30%;
    width: 50%;
    margin-top: 5%;
    margin-left: 20%;
    padding: 6px 10px 15px 10px;
    border: 1px solid black;
    border-radius: 10px;
    font-size: 1rem;
    background-color: rgba(104, 23, 227, 0.1);
  }

  .left{
    height: 30%;
    width: 50%;
    margin-top:-22%;
    margin-left: -60%;
    padding: 6px 10px 15px 10px;
    border: 1px solid black;
    border-radius: 10px;
    font-size: 1rem;
    background-color: rgba(104, 23, 227, 0.1);
  }
  .active{
    background-color:rgba(104, 23, 227, 0.3) ;
  }
  